<template>   
    <v-container >
                    <v-row  justify="center">
                        <v-col cols="8">
                            <v-card>
                                <v-row>
                                    <v-col cols="4">
                                    </v-col>
                                    <v-col cols="5" >
                                        <div class="header" >
                                            <h1>Change Password</h1>
                                        </div>
                                        <div class="form">
                                            <v-text-field
                                                v-model="form.old"
                                                label="Old Password"
                                                filled
                                                rounded
                                                dense
                                                :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                                                :type="show1 ? 'text' : 'password'"
                                                @click:append="show1 = !show1"
                                            ></v-text-field>
                                            <v-text-field
                                                v-model="form.password"
                                                label="New Password"
                                                filled
                                                rounded
                                                dense
                                                :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
                                                :type="show ? 'text' : 'password'"
                                                @click:append="show = !show1"
                                            ></v-text-field>
                                            <v-text-field
                                                v-model="form.conPass"
                                                label="Confirm Password"
                                                filled
                                                rounded
                                                dense
                                                :append-icon="show2 ? 'mdi-eye' : 'mdi-eye-off'"
                                                :type="show2 ? 'text' : 'password'"
                                                @click:append="show2 = !show1"
                                            ></v-text-field>
                                            <v-btn @click="cekForm()" block color="yellow" rounded class="elevation-0" height=40>Update Password</v-btn>
                                            
                                        </div>
                                    </v-col>
                                    
                                </v-row>
                            </v-card>
                        </v-col>
                    </v-row>
                    <v-snackbar       
                    v-model="snackbar" 
                    :color="color"       
                    :multi-line="true"       
                    :timeout="3000"     
                    >       
                    {{ text }}       
                    <v-btn         
                     dark         
                     text         
                     @click="snackbar = false"       
                    >         
                        Close       
                    </v-btn>     
                </v-snackbar>   
                </v-container>       
</template> 
<script> 
import { log } from 'util'
export default {    
    data () {       
        return {  
            edit: false,
            menu2: false,
            show: false,      
            show1: false,      
            show2: false,      
            dialog: false,  
            pass: false,       
            keyword: '', 
            items: ['Male', 'Female'],        
            users: [],         
            snackbar: false,          
            color: null,         
            text: '',          
            load: false, 
            form: {            
                old : '',  
                password: '',         
                conPass: ''      
            },         
            user : new FormData,         
            typeInput: 'new',        
            errors : '',         
            updatedId : '',       
        }     
    },     
    methods:{           
        sendData(){                       
            this.user.append('old', this.form.old);             
            this.user.append('password', this.form.password);         
            var uri =this.$apiUrl + '/User/password/' + localStorage.getItem('id')   
            this.$http.post(uri,this.user).then(response =>{               
                this.snackbar = true; //mengaktifkan snackbar               
                this.color = 'green'; //memberi warna snackbar               
                this.text = response.data.message; //memasukkan pesan ke snackbar               
                this.load = false;                 
                 //mengambil data user               
                this.resetForm()
            }).catch(error =>{               
                this.errors = error               
                this.snackbar = true;               
                this.text = 'Try Again';               
                this.color = 'red';               
                this.load = false;           
            })       
        },                  
        
        cekForm(){   
            console.log("wawaw")          
            if (this.form.conPass == this.form.password) {               
                this.sendData();
            }else{
                this.snackbar = true;               
                this.text = 'Password and Confirm Password not same';               
                this.color = 'red'; 
            }         
        },         
        resetForm(){             
            this.form = {               
                old : '',               
                conPass : '',               
                password : ''            
            }       
        }     
    },     
    mounted(){            
        }, 
    } 
</script> 
  
<style>
.header{
    margin-left: -40px;
    text-align: center;
    color: #000000;
    /* margin-bottom: 690px;
    margin-right: 654px; */
}
#title{
    font-family: Roboto;
}
#tagline{
    font-family: Roboto;
    font-style: normal;
    font-weight: 300;
    font-size: 20px;
    line-height: 33px;
}
.form{
    margin-left: -50px;
    margin-top: 100px;
    margin-bottom: 120px;
    color: rgba(0, 0, 0, 0.54);
}
</style>